import React from 'react';
/*import {
    LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend,
} from 'recharts';
*/
import {
    AreaChart, defs, linearGradient, stop, XAxis, YAxis, CartesianGrid, Area, Tooltip
} from 'recharts';

export const CDataChart = (props) => {
    return (
        <div>
        {props.data.length > 0 &&
        <AreaChart width={1100} height={250} data={props.data.map((item, index) => {return ({name: "точность ".concat(item.toString()).concat("%"), accuracy: item, amt: 100}) } )}
  margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
  <defs>

    <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
      <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8}/>
      <stop offset="95%" stopColor="#8884d8" stopOpacity={0}/>
    </linearGradient>
  </defs>
  <XAxis dataKey="name" />
  <YAxis />
  <CartesianGrid strokeDasharray="3 3" />
  <Tooltip />
  <Area type="monotone" dataKey="accuracy" stroke="#ffffff" fillOpacity={1} fill="url(#colorUv)" />
</AreaChart> }
</div>
    );
}