import BaseEntity from "../BaseEntity"
import {TrainingParamsBow} from "./TrainingParamsBow"
import {TrainingParamsCrossbow} from "./TrainingParamsCrossbow"
import {EWeaponTypes} from './EWeaponTypes'

export class TrainingParams extends BaseEntity {
    weaponType = EWeaponTypes.WEAPON_TYPE_BOW
    title = ""
    image = ""
    trainingParamsBow = null
    trainingParamsCrossbow = null

    constructor(obj) {
        super(obj)

        if (obj) {
            this.weaponType = obj.weaponType ? obj.weaponType : EWeaponTypes.WEAPON_TYPE_BOW
            this.title = obj.title ? obj.title : ""
            this.image = obj.image ? obj.image : ""
            this.trainingParamsBow = obj.trainingParamsBow ? new TrainingParamsBow(obj.trainingParamsBow) : null
            this.trainingParamsCrossbow = obj.trainingParamsCrossbow ? new TrainingParamsCrossbow(obj.trainingParamsCrossbow) : null
        }
    }
}