import TrainingParamsBase from "./TrainingParamsBase"
import {Target} from '../target/Target'

// параметры стендовой стрельбы из лука
export class TrainingParamsBow extends TrainingParamsBase {
    range = 0
    rangeMin = 0
    rangeMax = 0
    weaponType = null
    targetType = null

    constructor(obj) {
        super(obj)
        if (obj) {
            this.range = obj.range ? obj.range : 0
            this.rangeMin = obj.rangeMin ? obj.rangeMin : 0
            this.rangeMax = obj.rangeMax ? obj.rangeMax : 0
            this.weaponType = obj.weaponType ? obj.weaponType : null
            this.targetType = obj.targetType ? obj.targetType : null
        }
    }
}