import { Training } from "./TrainingDay"
import System from "../../core/System";

export class TrainingDayService {
    trainingDay = null

    constructor(trainingDay) {
        this.trainingDay = trainingDay ? trainingDay : null
    }

    countShots() {
        return this.trainingDay.shots.length
    }

    getTime() {
        return this.trainingDay.time
    }

    countAccuracyData(x = 300, y = 300) {
        const accuracyData = this.trainingDay.shots.map(shot => System.rangeBetweenPoint({x, y}, {x: shot.x, y: shot.y}))

        return accuracyData
    }

    countAverageAccuracy(x = 300, y = 300) {
        const accuracyData = this.countAccuracyData(x, y)

        if (accuracyData.length > 0) {
            return Math.round(accuracyData.reduce(function(sum, current) {
                return sum + current;
            }) / accuracyData.length)
        }

        return null
    }
}