import React from 'react'
import { CTrainingCreate } from '../components/main/training/trainingCreate/CTrainingCreate'
// временно
import { CTrainingList } from '../components/main/training/trainingList/CTrainingList'

function ShowTab(props) {
    if (props.mode === "new") {
        return <CTrainingCreate/>
    }

    if (props.mode === "list") {
        return <CTrainingList/>
    }

    return <CTrainingCreate/>
}

export class Training extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            mode: "new"
        }

        this.switchMode = this.switchMode.bind(this)
    }

    switchMode(mode) {
        this.setState({mode});
    }

    render() {
        return (
            <main>
                <div className="tab-panel">
                    <div className="tab-panel-switch">
                        <div className="container panel">
                            <a onClick={this.switchMode.bind(this, "new")} className={this.state.mode === "new" ? "btn": "btn btn-secondary"}>Новая тренировка</a>
                            <a onClick={this.switchMode.bind(this, "list")} className={this.state.mode === "list" ? "btn": "btn btn-secondary"}>Сохранённые тренировки</a>
                        </div>
                    </div>
                    <div className="tab-panel-body">
                        <ShowTab mode={this.state.mode}/>
                    </div>
                </div>

                
                
                {/*<WeaponMenu/>
                <TrainingParams/>
                <Target/>*/}
            </main>
        )
    }
} 