import React, {useEffect} from 'react'
import {Image, Box, Text, TextInput, Grommet} from 'grommet'
import _ from 'lodash'

export const CImageSelect = (props) => {
    const [value, setValue] = React.useState(props.data && props.data.length ? _.first(props.data) : null);

    useEffect(() => {
        props.onChoose(value)
    }, []);

    return (
        <Box className="image-select">
            <Box className="image-select-container">
                {props.data.map(item => {
                    return (
                        <Box className="image-select-item">
                            <Image
                                onClick={event => { setValue(item); props.onChoose(item)}}
                                className={value && item.uuid === value.uuid ? "image-select-item-image selected" : "image-select-item-image"}
                                src={item.icon}
                            />
                            <Text className="image-select-item-text">{item.label}</Text>
                        </Box>
                    )
                })}
            </Box>

        </Box>
    );
}