import React from 'react'
import { shot } from '../../../domain/entity/shot'
import { EWeaponTypes } from '../../../domain/entity/trainingParams/EWeaponTypes'

export class СTarget extends React.Component {
    

    constructor(props) {
        super(props)
        this.state = {
            shots: []
        }

        this.updateCanvas = this.updateCanvas.bind(this)
        this.onTargetClick = this.onTargetClick.bind(this)
    }

    componentDidMount() {
        this.updateCanvas()
    }

    componentDidUpdate() {
        this.updateCanvas()
    }

    updateCanvas() {
        const target = this.refs.targetCanvas.getContext('2d')
        const img = new Image()

        if (this.props.trainingParams.weaponType === EWeaponTypes.WEAPON_TYPE_BOW) {
            img.src = this.props.trainingParams.trainingParamsBow.targetType.image
        }

        if (this.props.trainingParams.weaponType === EWeaponTypes.WEAPON_TYPE_CROSSBOW) {
            img.src = this.props.trainingParams.trainingParamsCrossbow.targetType.image
        }       

        img.onload = () => {
            target.drawImage(img, 0, 0, 600, 600)

            this.state.shots.forEach(shot => {
                target.beginPath()
                target.arc(shot.x, shot.y, shot.radius, 0, 2*Math.PI, false)
	            target.fillStyle = 'rgba(100,150,185,0.5)'
	            target.fill()
	            target.lineWidth = 1
	            target.strokeStyle = 'black'
                target.stroke()
                
                target.beginPath()
                target.arc(shot.x, shot.y, 1, 0, 2*Math.PI, false)
	            target.fillStyle = 'black'
	            target.fill()
	            target.lineWidth = 1
	            target.strokeStyle = 'black'
	            target.stroke()
            });
        };
    }

    async onTargetClick(e) {
        const target = await this.refs.targetCanvas
        const rect = await target.getBoundingClientRect()
        const x = await e.clientX - rect.left
        const y = await e.clientY - rect.top

        e.preventDefault()

        await this.setState({
            shots: this.state.shots.concat(new shot({x, y, radius: 10}))
        })

        this.props.onSetShot(this.state.shots)

        await this.componentDidUpdate()
    }

    render() {
        return (
            <canvas onClick={this.onTargetClick} id="targetCanvas" ref="targetCanvas" width={600} height={600}/>
        )
    }
}