import React from 'react'
import {CTrainingListItemBow} from './CTrainingListItemBow'
import {CTrainingListItemCrossbow} from './CTrainingListItemCrossbow'
import {CTrainingListEmpty} from './CTrainingListEmpty'
import {EWeaponTypes} from '../../../../domain/entity/trainingParams/EWeaponTypes'
import {CDialog} from "../../../interface/CDialog"

export class CTrainingList extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            trainingList: [],
            showDeleteDialog: false,
            trainingParams: null
        }

        this.deleteTrainingParams = this.deleteTrainingParams.bind(this)
        this.showDeleteDialog = this.showDeleteDialog.bind(this)
        this.closeDeleteDialog = this.closeDeleteDialog.bind(this)
    }

    /* хуки */

    componentWillMount() { 
        // получаем список тренировок
        const trainingList = JSON.parse(localStorage.getItem('trainingList'))

        if (trainingList) {
            this.setState({trainingList})
        }
    }

    /* методы */

    async deleteTrainingParams() {
        if (this.state.trainingParams && this.state.trainingParams.uuid) {
            const trainingList = await JSON.parse(localStorage.getItem('trainingList'))

            if (trainingList) {
                const trainingParamsIndex = trainingList.findIndex(item => item.uuid === this.state.trainingParams.uuid)

                if (trainingParamsIndex >= 0) {
                    trainingList.splice(trainingParamsIndex, 1)

                    await localStorage.setItem('trainingList', JSON.stringify(trainingList))

                    await this.setState({trainingList})

                    this.closeDeleteDialog()
                }
            }
        }
    }

    showDeleteDialog(trainingParams) {
        this.setState({trainingParams})
        this.setState({showDeleteDialog: true})
    }

    closeDeleteDialog() {
        this.setState({showDeleteDialog: false})
    }

    render() {
        return (
            <div className="container panel">
                <div className="training-list">
                    {this.state.trainingList.length > 0 && this.state.trainingList.map(item => {
                        if (item.weaponType == EWeaponTypes.WEAPON_TYPE_BOW) {
                            return <CTrainingListItemBow onDelete={this.showDeleteDialog} trainingParams={item}/>
                        } 

                        if (item.weaponType == EWeaponTypes.WEAPON_TYPE_CROSSBOW) {
                            return <CTrainingListItemCrossbow onDelete={this.showDeleteDialog} trainingParams={item}/>
                        } 

                        return <CTrainingListItemBow onDelete={this.showDeleteDialog} trainingParams={item}/>
                    })}

                    {this.state.trainingList.length == 0 && <CTrainingListEmpty text="Список тренировок пуст"/>}
                </div>
                <CDialog onConfirm={this.deleteTrainingParams} onClose={this.closeDeleteDialog} show={this.state.showDeleteDialog} message="Вы уверены,что хотите удалить тренировку?"/>
            </div>
        )
    }
} 