import BaseEntity from "../BaseEntity"

export class Weapon extends BaseEntity {
    weaponType = null
    label = ""
    image = ""
    icon = ""

    constructor(obj) {
        super(obj)

        if (obj) {
            this.weaponType = obj.weaponType ? obj.weaponType : null
            this.label = obj.label ? obj.label : ""
            this.image = obj.image ? obj.image : ""
            this.icon = obj.icon ? obj.icon : ""
        }
    }
}