import { TrainingParams } from "../trainingParams/TrainingParams"

export class Training extends TrainingParams {
    name = ""
    days = []

    constructor(obj) {
        super(obj)

        if (obj) {
            this.name = obj.name ? obj.name : ""
            this.days = obj.days ? obj.days : []
        }
    }

    createDay(day = null) {
        if (day) {
            this.days.push(day)
        }
    }
}