import React from 'react'
import System from '../../../../domain/core/System'
import {Box, Grid, Heading} from "grommet"
import {TrainingService} from '../../../../domain/entity/training/TrainingService'
import {CInfoBox} from '../../../interface/CInfoBox'
import {CDataChart} from "../../../interface/CDataChart";
import {TrainingDayService} from "../../../../domain/entity/training/TrainingDayService";

export class CTrainingStartFullStat extends React.Component {
    constructor(props) {
        super(props)

        this.displayTotalTime = this.displayTotalTime.bind(this)
        this.displayTotalDays = this.displayTotalDays.bind(this)
        this.displayTotalShots = this.displayTotalShots.bind(this)
        this.displayTotalAccuracy = this.displayTotalAccuracy.bind(this)
        this.displayAccuracyData = this.displayAccuracyData.bind(this)
    }

    displayTotalTime() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return System.secondsToTime(trainingService.countTotalTime())
        }

        return System.secondsToTime(0)
    }

    displayTotalDays() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return trainingService.countDays()
        }

        return 0
    }

    displayTotalShots() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return trainingService.countTotalShots()
        }

        return "-"
    }

    displayTotalAccuracy() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            if (trainingService.countAverageAccuracy() !== null) {
                return trainingService.countAverageAccuracy().toString().concat("%")
            }
        }

        return "-"
    }

    displayAccuracyData() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return trainingService.countAccuracyData()
        }

        return []
    }

    render() {
        return (
            <div className="container panel">

                <CDataChart data={this.displayAccuracyData()}/>
                <Grid
                    columns={['auto', 'auto', 'auto']}
                    rows={['auto','auto']}
                    gap="small"
                    areas={[
                        { name: 'time', start: [0, 0], end: [0, 1] },
                        { name: 'shots', start: [0, 1], end: [0, 1] },
                        { name: 'accuracy', start: [1, 1], end: [1, 1] },
                        { name: 'days', start: [2, 1], end: [2, 1] },
                    ]}
                    >
                    <Box gridArea="time" pad="medium">
                        <CInfoBox label="Общее время" value={this.displayTotalTime()}/>
                    </Box>
                    <Box gridArea="shots" pad="medium">
                        <CInfoBox label="Количество выстрелов" value={this.displayTotalShots()}/>
                    </Box>
                    <Box gridArea="accuracy" pad="medium">
                        <CInfoBox label="Точность стрельбы" value={this.displayTotalAccuracy()}/>
                    </Box>
                    <Box gridArea="days" pad="medium">
                        <CInfoBox label="Количество дней" value={this.displayTotalDays()}/>
                    </Box>
                </Grid>
            </div>
        )
    }
}