import React, {useState} from 'react'
import {config} from '../../../../config'
import { Button, Grommet} from 'grommet'
import { Notification } from 'grommet-icons'
// импортируем мокнутые данные
import {Mock} from '../../../../mock'

export class CTrainingCreateWeapon extends React.Component {
    constructor(props) {
        super(props)
    }

    onChoose(trainingParams) {
        const handleChoose = this.props.handleChoose;
        handleChoose(trainingParams)
    }

    render() {   
        return (
            <div className="container panel">
                <h3 className="caption">Выберите режим тренировки:</h3>
                <div>
                </div>
                <div className="service">
                    {Mock.trainingParamsList.map(trainingParams => {
                        return (
                            <div key={trainingParams.uuid} className={this.props.selectTrainingParams.weaponType === trainingParams.weaponType ? "service-box selected" : "service-box"} style={{background: "url(" + trainingParams.image + ")"}}>
                                <div className="service-box-header">
                                    {/*<div className="service-box-header-hit">Хит!</div>*/}
                                </div>
                                <div className="service-box-footer">
                                    <div className="service-box-footer-title">
                                        {trainingParams.title}
                                    </div>
                                    <div className="service-box-footer-caption">
                                        <a className="btn" onClick={(e) => {
                                            e.preventDefault()
                                            this.onChoose(trainingParams)
                                        }}>Выбрать</a>
                                    </div>
                                </div>
                            </div>
                        )
                     })}
                </div>
            </div>            
        )
    }
}
