import { Training } from "./Training"
import { TrainingDayService } from './TrainingDayService'

export class TrainingService {
    training = null

    constructor(training) {
        this.training = training ? training : null
    }

    countDays() {
        return this.training.days.length
    }

    countTotalShots() {
        return this.training.days.reduce(function(sum, currentDay) {
            const trainingDayService = new TrainingDayService(currentDay)

            return sum + Number(trainingDayService.countShots());
        }, 0)
    }

    countTotalTime() {
        return this.training.days.reduce(function(sum, currentDay) {
            const trainingDayService = new TrainingDayService(currentDay)

            return sum + trainingDayService.getTime();
        }, 0)
    }

    countAverageAccuracy() {
        if (this.training.days.length > 0) {
            return Math.round(this.training.days.reduce(function (sum, currentDay) {
                const trainingDayService = new TrainingDayService(currentDay)

                if (trainingDayService.countAverageAccuracy() !== null) {
                    return sum + Number(trainingDayService.countAverageAccuracy())
                }

                return sum
            }, 0) / this.training.days.length)
        }

        return null
    }

    countAccuracyData() {
        if (this.training.days.length > 0) {
            return this.training.days.map(day => {
                const trainingDayService = new TrainingDayService(day)

                if (trainingDayService.countAverageAccuracy()) {
                    return trainingDayService.countAverageAccuracy()
                }

                return 0
            })
        }

        return []
    }
}