import { CImageSelect } from '../../../interface/CImageSelect'
import { TrainingParams } from '../../../../domain/entity/trainingParams/TrainingParams'
import { ECrossbowTypes } from "../../../../domain/entity/trainingParams/ECrossbowTypes"
import {Mock} from '../../../../mock'

