export class CTrainingCreateParamsCrossbow extends React.Component {
    constructor(props) {
        super(props)

        this.state = new TrainingParams(props.trainingParams)

        this.saveTraining = this.saveTraining.bind(this)
        this.changeName = this.changeName.bind(this)
        this.chooseTarget = this.chooseTarget.bind(this)
        this.chooseWeapon = this.chooseWeapon.bind(this)
        this.changeRange = this.changeRange.bind(this)
    }

    saveTraining() {
        this.props.saveTraining(this.state)
    }

    async changeName(name) {
        await this.setState({name})
    }

    async chooseTarget(targetType) {
        const trainingParamsCrossbow = await Object.assign(this.state.trainingParamsCrossbow)
        trainingParamsCrossbow.targetType = await Object.assign(targetType)

        await this.setState({trainingParamsCrossbow: Object.assign(trainingParamsCrossbow)})
    }

    async chooseWeapon(weaponType) {
        const trainingParamsCrossbow = await Object.assign(this.state.trainingParamsCrossbow)
        trainingParamsCrossbow.weaponType = await Object.assign(weaponType)

        await this.setState({trainingParamsCrossbow: Object.assign(trainingParamsCrossbow)})
    }

    async changeRange(range) {
        const trainingParamsCrossbow = await Object.assign(this.state.trainingParamsCrossbow)
        trainingParamsCrossbow.range = range

        await this.setState({trainingParamsCrossbow: Object.assign(trainingParamsCrossbow)})
    }

    render() {
        return (
            <div className="container panel">
                <Grid
                    columns={['auto', 'auto']}
                    rows={['auto', 'auto', 'auto', 'auto', 'auto']}
                    gap="small"
                    areas={[
                        { name: 'title', start: [0, 0], end: [1, 0] },
                        { name: 'target', start: [0, 1], end: [1, 1] },
                        { name: 'type', start: [0, 2], end: [1, 2] },
                        { name: 'ranger', start: [0, 3], end: [1, 3] },
                        { name: 'tool', start: [0, 4], end: [1, 4] },
                    ]}
                >
                    <Box gridArea="title" pad="medium">
                        <Heading margin="none" textAlign="start" color="light-1" level="3">Назовите тренировку:</Heading>
                        <CInput onChange={(value) => {this.changeName(value)}}/>
                    </Box>
                    <Box gridArea="target" pad="medium">
                        <Heading margin="none" textAlign="start" color="light-1" level="3">Выбор мишени:</Heading>
                        <CImageSelect onChoose={this.chooseTarget} data={Mock.trainingTargetList}/>
                        {/*<CSelectImage values={ECrossbowTypes.entries().map(item => item.value)} onChange={(value) => {this.setState({trainingParamsBow: {targetType: value}})}}/>*/}
                    </Box>
                    <Box gridArea="type" pad="medium">
                        <Heading margin="none" textAlign="start" color="light-1" level="3">Тип арбалета:</Heading>
                        <CImageSelect onChoose={this.chooseWeapon} data={Mock.trainingWeaponCrossbowsList}/>
                    </Box>
                    <Box gridArea="ranger" pad="medium">
                        <Heading margin="none" textAlign="start" color="light-1" level="3">Расстояние до мишени:</Heading>
                        <CRanger onChange={(value) => {this.changeRange(value)}} min="0" max="100" name="Расстояние до мишени"/>
                    </Box>
                    <Box gridArea="tool" pad="medium">
                        <a onClick={this.saveTraining} className="btn">Создать тренировку</a>
                    </Box>
                </Grid>
            </div>
        )
    }
}