import React, {useState} from 'react'
import { RangeInput, Box, Heading, Grommet} from 'grommet'

export const CRanger = (props) => {
    const [value, setValue] = React.useState(0)
    const title = props.title ? props.title : "Значение"
    const unit = props.unit ? props.unit : "метров"

    return (
      <Box gridArea="title" pad="medium">
        <Heading margin="none" textAlign="start" color="light-1" level="6">{title.concat(' ').concat(value.toString()).concat(' ').concat(unit)}</Heading>
        <RangeInput
          className="ranger"
          value={value}
          min={props.min}
          max={props.max}
          name={props.name}
          step={1}
          onChange={event => {setValue(event.target.value); props.onChange(event.target.value)}}
        />
      </Box>
    );
  }