import BaseEntity from "../BaseEntity"
import {ETargetType} from './ETargetType'

export class Target extends BaseEntity {
    targetType = ETargetType.TARGET_TYPE_CLASSIC_ARCHERY
    image = null
    icon = null
    label = ""

    constructor(obj) {
        super(obj)

        if (obj) {
            this.targetType = obj.targetType ? obj.targetType : ETargetType.TARGET_TYPE_CLASSIC_ARCHERY
            this.image = obj.image ? obj.image : null
            this.icon = obj.icon ? obj.icon : null
            this.label = obj.label ? obj.label : ""
        }
    }
}