export default class Enum {
    #entries = []

    constructor(obj) {
        if (obj && typeof obj == 'object') {
            const entries = Object.entries(obj)

            this.#entries = entries

            for (const [key, value] of entries) {
                this[key] = value
            }

            Object.freeze(this)
        }
    }

    entries() {
        return this.#entries
    }
}