import React from 'react'
import System from '../../../../domain/core/System'
import { TrainingCurrent } from '../../../../domain/entity/training/TrainingCurrent'
import { TrainingDay } from '../../../../domain/entity/training/TrainingDay'
import { TrainingDayService } from '../../../../domain/entity/training/TrainingDayService'
import { CInfoBox } from '../../../interface/CInfoBox'
import { CTimer } from '../../../interface/CTimer'
import { СTarget } from '../../../main/training/CTarget'
import { CDataChart } from "../../../interface/CDataChart"
import {Box, Grid, Heading} from "grommet"
import _ from 'lodash'

export class CTrainingStartDayStat extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            trainingParams: null,
        }

        this.onSetShot = this.onSetShot.bind(this)
        this.closeTraining = this.closeTraining.bind(this)
        this.currentDay = this.currentDay.bind(this)
        this.displayCurrentTime = this.displayCurrentTime.bind(this)
        this.displayCurrentShot = this.displayCurrentShot.bind(this)
        this.displayCurrentAverageAccuracy = this.displayCurrentAverageAccuracy.bind(this)
        this.displayCurrentAccuracyData = this.displayCurrentAccuracyData.bind(this)
    }

    async componentWillMount() {
        const trainingCurrent = localStorage.getItem('trainingCurrent')
        
        if (trainingCurrent) {
            const training = new TrainingCurrent(JSON.parse(trainingCurrent))
         
            // получаем список созданных тренировок
            const trainingListStr = localStorage.getItem('trainingList')

            if (trainingListStr) {
                const trainingList = JSON.parse(trainingListStr)

                const trainingParams = trainingList.find(trainingParams => trainingParams.uuid === training.trainingParamsUuid)

                if (trainingParams) {
                    await this.setState({trainingParams})
                }
            }
        }    
    }

    componentDidMount() {
        this.refs.timer.reset()
        this.refs.timer.play()       
    }

    async componentWillUnmount() {
        //await this.closeTraining()
    }

    //
    currentDay() {
        if (this.state.trainingParams) {
            return _.last(this.state.trainingParams.days)
        }

        return null
    }

    async closeTraining() {
        if (this.state.trainingParams) {

            // получаем список созданных тренировок
            const trainingListStr = await localStorage.getItem('trainingList')

            if (trainingListStr) {
                const trainingList = await JSON.parse(trainingListStr)

                const trainingParams = await trainingList.find(trainingParams => trainingParams.uuid === this.state.trainingParams.uuid)

                if (trainingParams) {
                    const currentDay = this.currentDay()

                    currentDay.time = this.refs.timer.getTime()
                    trainingParams.days = this.state.trainingParams.days

                    await localStorage.setItem('trainingList', JSON.stringify(trainingList))

                    this.props.onUpdate()
                }
            }
        }
    }

    async onSetShot(shots) {
        const trainingParams = await Object.assign(this.state.trainingParams)

        const lastDay = await _.last(trainingParams.days)

        lastDay.shots = shots 

        await this.setState({trainingParams})
    }

    displayCurrentTime() {
        const currentDay = this.currentDay()

        if (currentDay) {
            const currentDayService = new TrainingDayService(currentDay)

            return System.secondsToTime(currentDayService.getTime())
        }

        return "-"
    }

    displayCurrentShot() {
        const currentDay = this.currentDay()

        if (currentDay) {
            const currentDayService = new TrainingDayService(currentDay)

            return currentDayService.countShots()
        }

        return "-"
    }

    displayCurrentAverageAccuracy() {
        const currentDay = this.currentDay()

        if (currentDay) {
            const currentDayService = new TrainingDayService(currentDay)
            const countAverageAccuracy = currentDayService.countAverageAccuracy()

            if (countAverageAccuracy) {

                return countAverageAccuracy.toString().concat("%")
            }
        }

        return "-"
    }

    displayCurrentAccuracyData() {
        const currentDay = this.currentDay()

        if (currentDay) {
            const currentDayService = new TrainingDayService(currentDay)

            return currentDayService.countAccuracyData()
        }

        return []       
    }

    render() {
        return (
            <div className="container"> 
                <div className="container panel">
                    <CTimer ref="timer"/>
                </div>
                <div className="container panel">
                    <СTarget onSetShot={this.onSetShot} trainingParams={this.state.trainingParams}/>
                    <CDataChart data={this.displayCurrentAccuracyData()}/>
                </div>
                <div className="container panel">
                    <Grid
                        columns={['auto', 'auto']}
                        rows={['auto']}
                        gap="small"
                        areas={[
                            { name: 'shots', start: [0, 0], end: [0, 0] },
                            { name: 'accuracy', start: [1, 0], end: [1, 0] },
                        ]}
                        >
                        <Box gridArea="shots" pad="medium">
                            <CInfoBox label="Количество выстрелов" value={this.displayCurrentShot()}/>
                        </Box>
                        <Box gridArea="accuracy" pad="medium">
                            <CInfoBox label="Точность стрельбы" value={this.displayCurrentAverageAccuracy()}/>
                        </Box>
                    </Grid>
                    </div>
                <div className="container panel">
                    <a className="btn" onClick={this.closeTraining}>Завершить тренировку</a>
                </div>
            </div>
        )
    }
}