import React from 'react'
import {Button, Card, CardBody, CardHeader, Form, Text, Box} from 'grommet'

export class CDialog extends React.Component {
    constructor(props) {
        super(props)

        this.onConfirm = this.onConfirm.bind(this)
        this.onClose = this.onClose.bind(this)
    }

    onConfirm(e) {
        e.preventDefault()

        this.props.onConfirm()
    }

    onClose(e) {
        e.preventDefault()

        this.props.onClose()
    }

    render() {
        return (
            <div className={this.props.show ? "message-nightbox" : "message-nightbox hidden"}>
                <Card className="message-box" width="medium" background="light-1">
                    {/*<CardHeader className="pa-5 mb-15 text-medium center text-white"
                                pad="medium">{this.props.title ? this.props.title : "Ошибка"}</CardHeader>*/}
                    <CardBody className="message-box-body" style={{paddingBottom: "10px"}}>
                        <Form>
                            <Box pad="small">
                                <Text className="message-box-text">{this.props.message ? this.props.message : ""}</Text>
                            </Box>
                            <Box className="message-box-tools" pad="small">
                                <Button className="message-box-btn" onClick={this.onConfirm} type="button" label="Да"/>
                                <Button className="message-box-btn" onClick={this.onClose} type="button" label="Нет"/>
                            </Box>
                        </Form>
                    </CardBody>
                </Card>
            </div>
        )
    }
}