import React from 'react'
import {TrainingCurrent} from '../../../../domain/entity/training/TrainingCurrent'
import {TrainingService} from "../../../../domain/entity/training/TrainingService";

export class CTrainingListItemBow extends React.Component {
    constructor(props) {
        super(props)

        this.deleteTrainingParams = this.deleteTrainingParams.bind(this)
        this.openTraining = this.openTraining.bind(this)
        this.displayTotalDays = this.displayTotalDays.bind(this)
        this.displayTotalShots = this.displayTotalShots.bind(this)
        this.displayTotalAccuracy = this.displayTotalAccuracy.bind(this)
    }

    async openTraining() {
        const trainingCurrent = new TrainingCurrent({
            trainingParamsUuid: this.props.trainingParams.uuid,   
        })

        await localStorage.setItem('trainingCurrent', JSON.stringify(trainingCurrent))
        
        document.location = '/training_start'
    }

    deleteTrainingParams(e) {
        e.preventDefault()

        this.props.onDelete(this.props.trainingParams)
    }

    displayTotalDays() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return trainingService.countDays()
        }

        return 0
    }

    displayTotalShots() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            return trainingService.countTotalShots()
        }

        return "-"
    }

    displayTotalAccuracy() {
        if (this.props.trainingParams) {
            const trainingService = new TrainingService(this.props.trainingParams)

            if (trainingService.countAverageAccuracy() !== null) {
                return trainingService.countAverageAccuracy().toString().concat("%")
            }
        }

        return "-"
    }

    render() {
        return (
            <div className="training-box-container">
                <div className="training-box">
                    <div className="training-box-avatar">
                        <div className="training-box-avatar-img" style={{backgroundImage: 'url(' + this.props.trainingParams.image + ')'}}></div>
                    </div>
                    <div className="training-box-info">
                        <div className="training-box-info-title">
                            {this.props.trainingParams.name}
                        </div>
                        <div className="training-box-info-type">
                            Традиционная стрельба из лука
                        </div>
                        <div className="training-box-info-params">
                            <p>
                                Тип лука: {this.props.trainingParams.trainingParamsBow.weaponType.label}
                            </p>
                            <p>
                                Тип мишени: {this.props.trainingParams.trainingParamsBow.targetType.label}
                            </p>
                            <p>
                                Количество выстрелов: {this.displayTotalShots()}
                            </p>
                            <p>
                                Средняя точность: {this.displayTotalAccuracy()}
                            </p>
                        </div>
                        <div className="training-box-info-params">
                            Расстояние до мишени: {this.props.trainingParams.trainingParamsBow.range} м.
                        </div>
                        <div className="training-box-info-tools">
                            <a onClick={this.openTraining} className="btn">Начать тренировку</a>
                        </div>
                    </div>
                </div>
                <a onClick={this.deleteTrainingParams} className="training-box-close">&#215;</a>
            </div>
        )
    }
}