import React from 'react'
import {Box, Text, Grommet} from 'grommet'

export class CInfoBox extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <Box className="info-box">
                <Box className="info-box-label">
                    <Text className="info-box-label-text">{this.props.label}</Text>
                </Box>
                <Box className="info-box-value">
                    <Text className="info-box-value-text">{this.props.value}</Text>
                </Box>
            </Box>
        )
    }
}