import React from 'react'
import {config} from '../../../../config'
import {CTrainingCreateWeapon} from './CTrainingCreateWeapon'
import {CTrainingCreateParamsBow} from "./CTrainingCreateParamsBow"
import {CTrainingCreateParamsCrossbow} from "./CTrainingCreateParamsCrossbow"
import {TrainingParams} from '../../../../domain/entity/trainingParams/TrainingParams'
import {Training} from '../../../../domain/entity/training/Training'
import {TrainingCurrent} from '../../../../domain/entity/training/TrainingCurrent'
import {EWeaponTypes} from '../../../../domain/entity/trainingParams/EWeaponTypes'
import System from '../../../../domain/core/System'
// импортируем мокнутые данные
import {Mock} from '../../../../mock'

const ShowTrainingParams = (props) => {
    if (props.trainingParams) {
        switch (props.trainingParams.weaponType) {
            case EWeaponTypes.WEAPON_TYPE_BOW:
                return <CTrainingCreateParamsBow saveTraining={props.saveTraining} trainingParams={props.trainingParams}/>
            case EWeaponTypes.WEAPON_TYPE_CROSSBOW:
                return <CTrainingCreateParamsCrossbow saveTraining={props.saveTraining} trainingParams={props.trainingParams}/>
        }
    }

    return <CTrainingCreateParamsBow saveTraining={props.saveTraining} trainingParams={props.trainingParams}/>
}

export class CTrainingCreate extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            trainingParams: Mock.trainingParamsList[0],
            trainingList: null
        }

        this.onChoose = this.onChoose.bind(this)
        this.createTraining = this.createTraining.bind(this)
    }

    // выбор режима тренировка (лук, арбалет итд)
    async onChoose(trainingParams) {
        await this.setState({trainingParams: new TrainingParams(trainingParams)})
    }

    // создание новой тренировки
    async createTraining(params) {
        params = new Training(params)

        // временно генерим уникальный id (в последствии он будет генериться на стороне сервера по запросу)
        params.uuid = System.uuid()

        // сохраняем в localstorage текущую тренировку
        //await localStorage.setItem('currentTraining', JSON.stringify(params));

        //const currentTraining = new Training({trainingParamsUuid: params.uuid})

        //await localStorage.setItem('currentTraining2', JSON.stringify(currentTraining))

        // получаем массив тренировок из localStorage
        const trainingList = await JSON.parse(localStorage.getItem('trainingList'))

        if (trainingList) {
            await this.setState({trainingList})
        }

        if (this.state.trainingList) {
            await this.setState({trainingList: this.state.trainingList.concat([params])})
        }

        if (!this.state.trainingList) {
            await this.setState({trainingList: [params]})
        }

        // сохраняем в localstorage список тренировок
        await localStorage.setItem('trainingList', JSON.stringify(this.state.trainingList));

        const trainingCurrent = new TrainingCurrent({
            trainingParamsUuid: params.uuid,   
        })

        await localStorage.setItem('trainingCurrent', JSON.stringify(trainingCurrent))
        
        document.location = '/training_start'

    }

    render() {
        return (
            <div className="container">
                <CTrainingCreateWeapon selectTrainingParams={this.state.trainingParams} handleChoose={this.onChoose}/>
                <ShowTrainingParams saveTraining={this.createTraining} trainingParams={this.state.trainingParams}/>
            </div>
        )
    }
}