import React from 'react'
import { TrainingCurrent } from '../domain/entity/training/TrainingCurrent'
import System from '../domain/core/System'
import _ from 'lodash'
import { TrainingDay } from "../domain/entity/training/TrainingDay"
import { CTrainingStartDayStat } from '../components/main/training/trainingStart/CTrainingStartDayStat'
import { CTrainingStartFullStat } from '../components/main/training/trainingStart/CTrainingStartFullStat'

export class InTrain extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            isTrainingOpen: false,
            trainingParams: null
        }

        this.openTraining = this.openTraining.bind(this)
        this.closeTraining = this.closeTraining.bind(this)
        this.update = this.update.bind(this)
    }

    async componentWillMount() {
        const trainingCurrent = localStorage.getItem('trainingCurrent')
        
        if (trainingCurrent) {
            const training = new TrainingCurrent(JSON.parse(trainingCurrent))
         
            // получаем список созданных тренировок
            const trainingListStr = localStorage.getItem('trainingList')

            if (trainingListStr) {
                const trainingList = JSON.parse(trainingListStr)

                const trainingParams = trainingList.find(trainingParams => trainingParams.uuid === training.trainingParamsUuid)

                if (trainingParams) {
                    await this.setState({trainingParams})
                }
            }
        }    
    }

    async update() {
        const trainingCurrent = this.state.trainingParams

        if (trainingCurrent) {
            // получаем список созданных тренировок
            const trainingListStr = localStorage.getItem('trainingList')

            if (trainingListStr) {
                const trainingList = JSON.parse(trainingListStr)

                const trainingParams = trainingList.find(trainingParams => trainingParams.uuid === trainingCurrent.uuid)

                if (trainingParams) {
                    await this.setState({trainingParams})

                    await this.setState({isTrainingOpen: false})
                }
            }
        }
    }

    openTraining() {
        // добавляем новый день
        const trainingListStr = localStorage.getItem('trainingList')

        if (trainingListStr) {
            const trainingList = JSON.parse(trainingListStr)

            const trainingParams = trainingList.find(trainingParams => trainingParams.uuid === this.state.trainingParams.uuid)

            if (trainingParams) {
                trainingParams.days.push(new TrainingDay())

                localStorage.setItem('trainingList', JSON.stringify(trainingList))

                this.setState({trainingParams})

                this.setState({isTrainingOpen: true})
            }
        }
    }

    async closeTraining() {
        await this.setState({isTrainingOpen: false})
    }

    render() {
        return (
            <main>
                {!this.state.isTrainingOpen && <div className="container panel">
                    <a onClick={this.openTraining} className="btn">Начать тренировку</a>
                </div>}

                {this.state.isTrainingOpen &&
                    <CTrainingStartDayStat onUpdate={this.update}/>
                }

                {!this.state.isTrainingOpen && 
                    <CTrainingStartFullStat trainingParams={this.state.trainingParams}/>
                }
            </main>
        )
    }
}