import BaseEntity from "../BaseEntity";
import {shot} from "../shot";

export class TrainingDay extends BaseEntity {
    shots = []
    time = null

    constructor(obj) {
        super(obj)

        if (obj) {
            this.time = obj.time ? obj.time : null
        }
    }

    // отметка попадания
    setShot(shot = null) {
        if (shot) {
            this.shots.push(shot)
        }
    }

    removeShot(shot = null) {
        if (shot) {
            const indexShot = this.shots.findIndex(item => item.uuid === shot.uuid)

            if (indexShot !== -1) {
                this.shots.splice(indexShot)
            }
        }
    }
}