import React from 'react'

function CShowHeaderNavigator(props) {
    let currentLocation = window.location.pathname

    return (
        <div className="top-menu container-fluid">  
            <ul>
                {props.navigator.map(item => {
                    return (
                        <li className={item.route === currentLocation? "selected" : ""}><a href={item.route}>{item.menu}</a></li>
                    )
                })}
            </ul>
        </div>  
    )
}

