export default class System {
    static uuid() {
        return Math.random().toString(36).substr(2, 9)
    }

    // сделать такие параметры для всех функций
    static secondsToTime(aSeconds) {
        let hours = Math.trunc(aSeconds / 3600)
        let minutes = Math.trunc((aSeconds - (hours * 3600)) / 60)
        let seconds = aSeconds - hours * 3600 - minutes * 60

        hours = hours <= 9 ? "0".concat(hours) : hours
        minutes = minutes <= 9 ? "0".concat(minutes) : minutes
        seconds = seconds <= 9 ? "0".concat(seconds) : seconds

        return hours + ':' + minutes + ':' + seconds
    }

    // расстояние между точками
    static rangeBetweenPoint(point1, point2) {
        return Math.round(((300 - Math.sqrt((point1.x - point2.x) ** 2 + (point1.y - point2.y) ** 2)) / 300 * 100))
    }
}