export class TrainingCurrent {
    trainingParamsUuid = null
    isTrainingDayOpen = false

    constructor(obj) {
        if (obj) {
            this.trainingParamsUuid = obj.trainingParamsUuid ? obj.trainingParamsUuid : null
            this.isTrainingDayOpen = obj.isTrainingDayOpen ? obj.isTrainingDayOpen : false
        }
    }
}